import { createStore, combineReducers } from 'redux';
import reducerArea from './chooseArea/reducer.js'
import reducerLogin from './login/reducer.js';
import reducerAppraise from './user/reducer';

const initValues = {
    area: '广州',
    user: ['', ''],
    appraises:{
        appraise:false,
        content:''
    }
}
const reducers = combineReducers({
    area: reducerArea,
    user: reducerLogin,
    appraises:reducerAppraise

})

const store = createStore(reducers, initValues);

export default store;