import React, { Component } from 'react';
import {connect} from 'react-redux';
import './login.less';
import { login } from './action';
class Login extends Component {
    constructor(props) {
        super(props);
        this.state={
            id:'',
            password:''
        }
        this.changeId=this.changeId.bind(this);
        this.changePass=this.changePass.bind(this);
        this.loginUseState=this.loginUseState.bind(this);
        this.historyBack=this.historyBack.bind(this);
    }
    historyBack(){
        window.history.back();
    }
    changeId(event){
        this.setState({
            id:event.target.value,
            password:this.state.password
        });
    }
    changePass(event){
        this.setState({
            id:this.state.id,
            password:event.target.value
        });
    }
    loginUseState(){
        console.log(this.state.id,this.state.password)
        this.props.onLogin(this.state.id,this.state.password);
        window.location.hash='/';
    }
    render() {
        return (
            <div className='login'>
                <header>
                    <span onClick={this.historyBack}>返回</span>
                    <p>登陆</p>
                </header>
                <main>
                    <div>
                        <input type='text' placeholder='输入手机号' defaultValue='' onInput={this.changeId}></input>
                        <input type='text' placeholder='输入验证码' defaultValue='' onInput={this.changePass}></input>
                        <button>发送验证码</button>
                        <button onClick={this.loginUseState}>登录</button>
                    </div>
                </main>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        onLogin: (id,pass) => {
            dispatch(login(id,pass))
        }
    }
}

export default connect('', mapDispatchToProps)(Login);
