import React from 'react';
import './details.less';
import image from '../image/03.jpg';
import { connect } from 'react-redux';

const Details = ({ user }) => {
    const historyBack = () => {
        window.history.back();
    }

    const buy = () => {
        if (user[0]) {
            console.log('购买成功');
            window.location.hash = '/user';
        } else {
            window.location.hash = '/login';
        }
    }

    const changeClass = (event) => {
        if (event.target.className) {
            event.target.className = '';
            event.target.firstChild.nodeValue = '收藏';
        } else {
            event.target.className = 'done';
            event.target.firstChild.nodeValue = '已收藏';
        }
    }

    return (
        <div className='details'>
            <header>
                <span onClick={historyBack}>返回</span>
                <p>商户详情</p>
            </header>
            <main>
                <div className='main-div'>
                    <img src={image} alt='图片'></img>
                    <div>
                        <p>天下第一锅</p>
                        <span>89%好评</span>
                        <span>¥88</span>
                        <p>重庆 麻辣火锅</p>
                    </div>
                </div>
                <ul>
                    <li>营业时间 11：00-20：00</li>
                    <li>电话订购 11：00-19：00</li>
                    <li>网络订购 11：00-19：00</li>
                </ul>
                <div className='main-foot'>
                    <button onClick={changeClass}>收藏</button>
                    <button onClick={buy}>购买</button>
                </div>
            </main>
            <article>
                <p>用户点评</p>
                <div>
                    <p>用户133****3333</p>
                    <p>好评</p>
                    <p>非常好吃，不错</p>
                </div>
                <div>
                    <p>用户133****3333</p>
                    <p>好评</p>
                    <p>非常好吃，不错</p>
                </div>
                <div>
                    <p>用户133****3333</p>
                    <p>好评</p>
                    <p>非常好吃，不错</p>
                </div>
            </article>
        </div>
    )
}

const mapStateToProps = (state, ownProps) => {
    return {
        user: state.user
    }
}



export default connect(mapStateToProps, '')(Details);
