import React, { Component } from 'react';
import './user.less';
import image from '../image/04.jpg';
import { connect } from 'react-redux';
import { onAppraise } from './action';

class User extends Component {
    constructor(props) {
        super(props);
        this.historyBack = this.historyBack.bind(this);
        this.appraiseStart = this.appraiseStart.bind(this);
        this.onAppraise = this.onAppraise.bind(this);
        this.onRadioChange = this.onRadioChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.state = {
            appraise: false,
            content: ''
        }
    }
    historyBack() {
        window.history.back();
    }

    appraiseStart(event) {
        if (event.target.firstChild.nodeValue === '已评价') {
            return;
        }
        if (event.target.firstChild.nodeValue === '取消') {
            let parDiv = event.target.parentNode.parentNode;
            let butt = parDiv.previousSibling.getElementsByClassName('butt')[0];
            butt.firstChild.nodeValue = '评价';
            parDiv.style.display = 'none';
            return;
        }
        let divAppra = event.target.parentNode.parentNode.nextSibling;
        if (!divAppra.style.display || divAppra.style.display === 'none') {
            event.target.firstChild.nodeValue = '评价中';
            divAppra.style.display = 'block';
        } else {
            event.target.firstChild.nodeValue = '评价';
            divAppra.style.display = 'none';
        }
    }
    onAppraise(event) {
        this.setState({
            appraise: this.state.appraise,
            content: event.target.value
        });
    }
    onRadioChange(event) {
        let bool = false;
        if (event.target.checked && event.target.nextSibling.nodeValue === '好评') {
            bool = true;
        }
        this.setState({
            appraise: bool,
            content: this.state.content
        });
    }
    onSubmit(event) {
        this.props.onAppraiseUpdate(this.state.content, this.state.appraise);
        event.target.nextSibling.click();
        let parDiv = event.target.parentNode.parentNode;
        let butt = parDiv.previousSibling.getElementsByClassName('butt')[0];
        butt.firstChild.nodeValue = '已评价';
    }
    render() {
        return (
            <div className='user'>
                <header>
                    <span onClick={this.historyBack}>返回</span>
                    <p>用户主页</p>
                </header>
                <div>
                    <p>用户{this.props.user[0]}</p>
                    <p>{this.props.area}</p>
                </div>
                <section>
                    <p>您的订单</p>
                    <div>
                        <div className='section'>
                            <div className='section-div'>
                                <img src={image} alt='图片'></img>
                                <div>
                                    <p>商户：汉堡</p>
                                    <p>大王</p>
                                    <p>数量：3</p>
                                    <p>价格：¥167</p>
                                </div>
                                <div>
                                    <button className='butt' onClick={this.appraiseStart}>评价</button>
                                </div>
                            </div>
                            <div className='appraise'>
                                <textarea placeholder='请输入评价' onInput={this.onAppraise}></textarea>
                                <span><input type='radio' name='appraise' onChange={this.onRadioChange}></input>好评</span>
                                <span><input type='radio' name='appraise' onChange={this.onRadioChange}></input>差评</span>
                                <div>
                                    <button onClick={this.onSubmit}>提交</button>
                                    <button onClick={this.appraiseStart}>取消</button>
                                </div>
                            </div>
                            <div className='section-div'>
                                <img src={image} alt='图片'></img>
                                <div>
                                    <p>商户：汉堡</p>
                                    <p>大王</p>
                                    <p>数量：3</p>
                                    <p>价格：¥167</p>
                                </div>
                                <div>
                                    <button className='butt' onClick={this.appraiseStart}>评价</button>
                                </div>
                            </div>
                            <div className='appraise'>
                                <textarea placeholder='请输入评价' onInput={this.onAppraise}></textarea>
                                <span><input type='radio' name='appraise' onChange={this.onRadioChange}></input>好评</span>
                                <span><input type='radio' name='appraise' onChange={this.onRadioChange}></input>差评</span>
                                <div>
                                    <button onClick={this.onSubmit}>提交</button>
                                    <button onClick={this.appraiseStart}>取消</button>
                                </div>
                            </div>
                            <div className='section-div'>
                                <img src={image} alt='图片'></img>
                                <div>
                                    <p>商户：汉堡</p>
                                    <p>大王</p>
                                    <p>数量：3</p>
                                    <p>价格：¥167</p>
                                </div>
                                <div>
                                    <button className='butt' onClick={this.appraiseStart}>评价</button>
                                </div>
                            </div>
                            <div className='appraise'>
                                <textarea placeholder='请输入评价' onInput={this.onAppraise}></textarea>
                                <span><input type='radio' name='appraise' onChange={this.onRadioChange}></input>好评</span>
                                <span><input type='radio' name='appraise' onChange={this.onRadioChange}></input>差评</span>
                                <div>
                                    <button onClick={this.onSubmit}>提交</button>
                                    <button onClick={this.appraiseStart}>取消</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        area: state.area,
        user: state.user
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        onAppraiseUpdate: (content, bool) => {
            dispatch(onAppraise(content, bool))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(User);
