import React, { Component } from 'react';
import { connect } from 'react-redux';
import './chooseArea.less';
import { chooseArea } from './action';

const ChooseArea = ({ onChoose,area }) => {
    const chooseArea = (event) => {
        console.log(event.target.firstChild.nodeValue)
        onChoose(event.target.firstChild.nodeValue);
        window.location.hash = '/';
    }
    const historyBack=()=>{
        window.history.back();
    }
    return (
        <div className='chooseArea'>
            <header>
                <span onClick={historyBack}>返回</span>
                <p>选择城市</p>
            </header>
            <section>
                <span>{area}</span>
            </section>
            <nav>
                <p>热门城市</p>
                <div onClick={chooseArea}>
                    <button>北京</button>
                    <button>上海</button>
                    <button>杭州</button>
                    <button>广州</button>
                    <button>苏州</button>
                    <button>深圳</button>
                    <button>南京</button>
                    <button>天津</button>
                    <button>重庆</button>
                </div>
            </nav>
        </div>
    )
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        onChoose: (text) => {
            dispatch(chooseArea(text))
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        area: state.area
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ChooseArea)
